import React,{Component} from 'react';
import {Link} from "react-router-dom";
import Cookies from 'universal-cookie';
import { Button } from 'reactstrap';
import logo from './colloquium.svg';

// import {ButtonToolbar} from 'react-bootstrap';


class Questions extends Component{
    cookies = new Cookies();
    constructor(){
    super();
    this.state={
        // auth_url : 'https://ramyareddy16.herokuapp.com/api-basictoken-auth/',
        // jwt_url : 'https://ramyareddy16.herokuapp.com/api-jwttoken-auth/',
        auth_url : 'http://127.0.0.1:8000/api-basictoken-auth/',
        jwt_url : 'http://127.0.0.1:8000/api-jwttoken-auth/',
        QuestionsList:[],
        search : '',
    }
    }   


    logout = (props) =>
    {
        this.cookies.remove('userJwtToken');
        this.cookies.remove('username');
        console.log(this.cookies.get('userJwtToken'));
        this.props.updateUsername('');
        this.props.updateStatus(false);
        this.setState(prev => ( {buttonName : 'Login'}));
    }
    
    componentDidMount() {
        fetch('http://127.0.0.1:8000/forum/api/questions/', {
                method: 'get', 
               
                }).then(function(response) {
                    return response.json();
                })
                .then((myJson) => {
                    this.setState(prev => ( {QuestionsList : myJson}));
                })
                .catch(e => {console.log("Error occured in fetching..")});
    }

    updateSearch(event){
        this.setState({search:event.target.value.substr(0,20)});
    }
    
    render(){
        let filteredQuestions = this.state.QuestionsList.filter(
            (currentObj)=>{
                return currentObj.title.toLowerCase().indexOf(this.state.search.toLowerCase())!==-1;
            }
        );
        return(
            <div>
                <div>
                <nav className="navbar navbar-inverse" style={{backgroundcolor: "darkred"}}>
                    <div className="container-fluid">
                    <div className="navbar-header">
                        <img style={{float:"left",align: "left",padding: "10px"}} src={logo} alt="logo" className={"App-logo"} />
                        <a className="navbar-brand" href='/forum/templateview'>COLLOQUIUM</a>
                        <input type="text" style={{width:"60%"}} className="form-control" placeholder="&#xF002; Search...." 
                        value={this.state.search} name=" search" onChange={this.updateSearch.bind(this)}/>
                    </div>
        
                    <ul className="nav navbar-nav navbar-right">
                    <li><a href="/forum/templateview/addquestion"><span className="glyphicon glyphicon-plus"></span>Add Question</a></li>
                    {this.props.isAuthenticated?<div></div>:
                    <li><a href="/forum/templateview/register"><span className="glyphicon glyphicon-user"></span> Sign Up</a></li>}
                    {this.props.isAuthenticated?<div><button className={"btn btn-primary login-button"}
                             onClick={
                                    this.props.isAuthenticated?
                                    this.logout : this.login
                                }
                             >
                            { this.props.isAuthenticated? "Logout" : "Login Page"}
                        </button></div>:
                    <li><a href="/forum/templateview/login"><span className="glyphicon glyphicon-log-in"></span> Login</a></li>}
                    </ul>
                    </div>
                </nav>
                </div>

                {/* <div class = "toppnav"  align = "left" style={{float:"left",width:"35%", padding:"5px"}}>
                <input type="text" style={{fontFamily:'FontAwesome',width:"90%"}} placeholder="&#xF002;   Search......." name=" search" value={this.state.search} 
                onChange={this.updateSearch.bind(this)}/>
                </div> */}
                {/* <div class = "topnav" align ="right"  style={{float:"right"}}>
                <Link style={{textDecoration: 'none', color:'black',width:"30%",}} to={'/forum/templateview/addquestion'}>
                    <Button outline color="secondary">Add Question </Button></Link>{'      '}
                {this.props.isAuthenticated?<div></div>:
                    <Link style={{width:"10%",}} to={'/forum/templateview/register'}><button class="btn danger">SignUp</button></Link>}{'   '}
                {this.props.isAuthenticated?<div></div>:
                <Link style={{width:"10%",}} to={'/forum/templateview/login'}><button class="btn danger"> Login  </button></Link> }{'   '}
                </div> */}
                <div align= "center" style={{width:"15%"}}>
                <h2 > Questions List </h2>
                </div>
                <br/>
                <br/>
                {
                    filteredQuestions.map((currentObj) =>
                    <div class="card"  style={{border:"1px groove",width:"90%",position:'relative'}}>
                        <div class="container">
                        <h2><Link  to={'/forum/templateview/questions/'+currentObj.id +'/'}>{currentObj.title}</Link></h2>
                        <h5 style={{position:'absolute',bottom:'0',right:'10px'}}>
                        Asked By : {currentObj.user_profile.user.first_name}</h5>
                        </div>
                    </div>
                    )
                }
                
            </div>
        );
        }
    }

export default Questions;


